@import "../../scss/variables";

.card-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    .cards {
        background-color: $cardNavBgColor;
        margin: 1rem;
        padding: 1rem;
        border-radius: 6px;
        box-shadow: 3px 3px 4px rgba(0, 0, 0, 0.7);
        position: relative;
        overflow: hidden;
        &:hover .card-over {
            transform: translateY(0%)
        }
    }
    .title {
        color: white;
        padding: 1rem;
        font-size: 1.5rem;
        font-family: "Amatic SC", cursive;
    }
    img {
        width: 450px;
        height: 320px;
        object-fit: cover;
        filter: grayscale(0%);
        &:hover {
            filter: grayscale(100%);
        }
    }
    .card-over {
        color: white;
        position: absolute;
        bottom: 0;
        left: 0;
        background-color: rgba(0, 0, 0, 0.7);
        font-size: 1.5rem;
        font-family: 'Hubballi', cursive;
        padding: 1rem;
        transform: translateY(100%);
        transition: transform 0.8s ease-in-out;
    }
}